<template>
    <el-card>
        <div slot="header" class="clearfix">
            <span>菜单权限</span>
        </div>
        <el-alert title="Tips" type="warning" description="设置后台管理系统的左侧菜单，目前至多支持两层菜单，三层以上菜单将不会显示。" show-icon
            :closable="false">
        </el-alert>
        <el-table :data="tableData" row-key="permission_id" lazy border default-expand-all max-height="700"
            style="width: 100%">
            <el-table-column fixed="left" label="资源名称" width="200">
                <template slot-scope="scope">
                    {{ scope.row.resource_type_name === '操作' ? scope.row.permission_remark : scope.row.menu_name }}
                </template>
            </el-table-column>
            <el-table-column prop="icon_id" label="图标">
                <template slot-scope="scope">
                    <i :class="`el-icon-${scope.row.icon_name}`"></i>
                </template>
            </el-table-column>
            <el-table-column label="资源类型">
                <template slot-scope="scope">
                    <el-tag type="warning" v-if="scope.row.resource_type_name === '操作'">{{ scope.row.resource_type_name
                    }}</el-tag>
                    <el-tag v-else>{{ scope.row.resource_type_name }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="resource_type_code" label="资源代码" width="120">
            </el-table-column>
            <el-table-column prop="permission_code" label="权限代码" width="180">
            </el-table-column>
            <el-table-column prop="operation_name" label="操作名称" width="120">
            </el-table-column>
            <el-table-column prop="menu_name" label="菜单名称" width="200">
            </el-table-column>
            <el-table-column prop="menu_full_path" label="完整菜单地址" width="200">
            </el-table-column>
            <el-table-column prop="menu_order" label="菜单序号" width="120">
            </el-table-column>
            <el-table-column prop="permission_remark" label="备注" width="120">
            </el-table-column>
            <el-table-column fixed="right" label="操作" width="240">
                <template slot-scope="scope">
                    <el-button v-permission="'system:route:remove'" @click="openEditDialog(scope.row)" type="primary" plain>
                        <i class="el-icon-edit"></i></el-button>
                    <el-button v-permission="'system:menu:create'" @click="openInsertDialog(scope.row)"
                        v-if="scope.row.resource_type_name !== '操作'" type="success" plain>
                        <i class="el-icon-circle-plus-outline"></i> </el-button>
                    <el-button v-permission="'system:menu:setting'" @click="openIconDialog(scope.row)"
                        v-if="scope.row.resource_type_name !== '操作'" type="warning" plain>
                        <i class="el-icon-setting"></i></el-button>
                    <el-button v-permission="'system:menu:remove'" @click="handleRemove(scope.row)" type="danger" plain>
                        <i class="el-icon-delete"></i></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!-- 添加菜单 -->
        <el-dialog title="添加菜单" :visible.sync="insert_dialog_visible" @close="handleCloseDialog('insert_form')">
            <el-tabs v-model="activeName" type="card">
                <el-tab-pane label="用户管理" name="first">
                    <el-form :model="insert_form" ref="insert_form" :rules="rules" status-icon label-width="150px">
                        <el-form-item label="菜单名称" prop="menu_name">
                            <el-input type="text" v-model="insert_form.menu_name"></el-input>
                        </el-form-item>
                        <el-form-item label="关联路由" prop="route_id">
                            <el-cascader v-model="insert_form.route_id" :options="route_1st" :props="route_props"
                                clearable></el-cascader>
                        </el-form-item>
                        <el-form-item label="显示顺序" prop="menu_order">
                            <el-input type="text" v-model="insert_form.menu_order"></el-input>
                        </el-form-item>
                        <el-form-item label="权限代码" prop="permission_code">
                            <el-input type="text" v-model="insert_form.permission_code"></el-input>
                        </el-form-item>
                        <el-form-item label="权限备注" prop="permission_remark  ">
                            <el-input type="text" v-model="insert_form.permission_remark"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="操作按钮" name="second">
                    <el-form :model="insert_form" ref="insert_form" :rules="rules" status-icon label-width="150px">
                        <el-form-item label="操作类型">
                            <el-select v-model="insert_form.operation_id" placeholder="请选择操作按钮类型" prop="operation_name">
                                <el-option v-for="item in operation" :label="item.operation_name" :key="item.operation_id"
                                    :value="item.operation_id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="权限代码" prop="permission_code">
                            <el-input type="text" v-model="insert_form.permission_code"
                                placeholder="权限代码由父级权限和按钮权限组成"></el-input>
                        </el-form-item>
                        <el-form-item label="权限备注" prop="permission_remark  ">
                            <el-input type="text" v-model="insert_form.permission_remark"
                                placeholder="请输入权限备注信息"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
            <span slot="footer" class="dialog-footer">
                <el-button @click="insert_dialog_visible = false">取 消</el-button>
                <el-button type="primary" @click="handleInsert">添 加</el-button>
            </span>
        </el-dialog>

        <!-- 编辑菜单 -->
        <el-dialog title="编辑菜单" :visible.sync="edit_dialog_visible" @close="handleCloseDialog('edit_form')">
            <el-tabs v-model="activeName" type="card">
                <el-tab-pane label="用户管理" :disabled="edit_form.resource_type_code !== 'MENU'" name="first">
                    <el-form :model="edit_form" ref="edit_form" :rules="rules" status-icon label-width="150px">
                        <el-form-item label="菜单名称" prop="menu_name">
                            <el-input type="text" v-model="edit_form.menu_name"></el-input>
                        </el-form-item>
                        <el-form-item label="关联路由" prop="route_id">
                            <el-cascader v-model="edit_form.route_id" :options="route_1st" :props="route_props"
                                clearable></el-cascader>
                        </el-form-item>
                        <el-form-item label="显示顺序" prop="menu_order">
                            <el-input type="text" v-model="edit_form.menu_order"></el-input>
                        </el-form-item>
                        <el-form-item label="权限代码" prop="permission_code">
                            <el-input type="text" v-model="edit_form.permission_code"></el-input>
                        </el-form-item>
                        <el-form-item label="权限备注" prop="role_description">
                            <el-input type="text" v-model="edit_form.role_description"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="操作按钮" :disabled="edit_form.resource_type_code !== 'OPERATION'" name="second">
                    <el-form :model="edit_form" ref="edit_form" :rules="rules" status-icon label-width="150px">
                        <el-form-item label="操作类型">
                            <el-select v-model="edit_form.operation_id" placeholder="请选择操作按钮类型" prop="operation_id">
                                <el-option v-for="item in operation" :label="item.operation_name" :key="item.operation_id"
                                    :value="item.operation_id"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="权限代码" prop="permission_code">
                            <el-input type="text" v-model="edit_form.permission_code"
                                placeholder="权限代码由父级权限和按钮权限组成"></el-input>
                        </el-form-item>
                        <el-form-item label="权限备注" prop="role_description">
                            <el-input type="text" v-model="edit_form.role_description" placeholder="请输入权限备注信息"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
            </el-tabs>
            <span slot="footer" class="dialog-footer">
                <el-button @click="edit_dialog_visible = false">取 消</el-button>
                <el-button type="primary" @click="handleEdit">保 存</el-button>
            </span>
        </el-dialog>

        <!-- 设置图标  -->
        <el-dialog width="75%" title="选择图标" :visible.sync="icon_dialog_visible">
            <div class="outer">
                <div class="inter" :class="{ click: current_icon.icon_id === item.icon_id }" @click="handleInsertIcon(item)"
                    v-for="item in icon_list" :key="item.icon_id">
                    <div>
                        <i :class="`el-icon-${item.icon_name}`"
                            style="font-size: 26px; text-align: center; line-height: 70px;"></i>
                    </div>
                    <span>el-icon-{{ item.icon_name }}</span>
                </div>
            </div>
            <el-pagination @current-change="handleCurrentChange" style="float: right;" background layout="prev, pager, next"
                :total="280" :page-size="30">
            </el-pagination>
            <span slot="footer" class="dialog-footer">
                <el-button @click="icon_dialog_visible = false">取 消</el-button>
                <el-button type="primary" @click="handleIcon">添 加</el-button>
            </span>
        </el-dialog>
    </el-card>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex';
export default {
    name: 'list',
    data() {
        return {
            tableData: [],
            activeName: 'first',
            insert_dialog_visible: false,
            insert_form: {
                menu_name: "",
                route_id: "",
                menu_order: "",
                permission_code: "",
                permission_remark: "",
                resource_type_name: '',
                resource_type_code: '',
                operation_id: '',
                operation_name: '',
            },
            cascader: '',
            route_1st: '',
            route_props: {
                checkStrictly: true,
                label: 'route_name',
                value: 'route_id',
                emitPath: false,
            },
            edit_dialog_visible: false,
            edit_form: {
                menu_name: "",
                route_id: "",
                menu_order: "",
                permission_code: "",
                permission_remark: "",
                resource_type_name: '',
                resource_type_code: '',
                operation_id: '',
                operation_name: '',
            },
            current_menu: '',
            current_icon: '',
            icon_dialog_visible: false,
            rules: {
                menu_name: [
                    { required: true, message: "请输入菜单名称！", tigger: 'blur' },
                ],
                route_id: [
                    { type: 'number', required: true, message: "请选择与之关联的路由！", tigger: 'blur' },
                ],
                menu_order: [
                    { required: true, message: "请填写显示顺序！", tigger: 'blur' },
                ],
                permission_code: [
                    { required: true, message: "请输入权限代码！", tigger: 'blur' },
                ],
                role_description: [
                    { required: true, message: "请输入权限备注！", tigger: 'blur' },
                ],
            },


        }
    },
    created() {
        this.loadList();
        document.title = '菜单管理';
        // 加载级联选择器
        this.route_1st = this.tree_data;
    },
    watch: {
        tree_menu(newValue) {
            this.tableData = newValue;
        },
    },
    computed: {
        ...mapState('operation', ['operation']),
        ...mapGetters('route', ['tree_data']),
        ...mapGetters('menu', ['tree_menu']),
        ...mapState('menu', ['icon_list']),
    },
    methods: {
        // 加载菜单列表
        ...mapActions('menu', ['load_menu']),
        async loadList() {
            let { status, msg, data } = await this.load_menu({});
            if (status) {
                this.tableData = this.tree_menu;
            }
        },
        // 添加菜单、按钮
        ...mapActions('menu', ['create_menu']),
        ...mapActions('menu', ['create_operation']),
        // 打开添加弹窗
        openInsertDialog(menu) {
            this.insert_dialog_visible = true;
            this.current_menu = { ...menu };
            // 还原父级ID
            this.insert_form.parent_id = current_menu.permission_id;
            this.insert_form.permission_code = current_menu.permission_code;
        },
        handleInsert() {
            // 填充资源类型
            if (this.activeName === 'first') {
                this.insert_form.resource_type_name = '菜单'
                this.resource_type_code = 'MENU';
                this.$refs.insert_form.validate(async (valid) => {
                    if (valid) {
                        let { status, msg, data } = await this.create_menu({ ...this.insert_form });
                        if (status) {
                            // 关闭弹窗
                            this.insert_dialog_visible = false;
                            // 信息提示
                            this.$message.success(msg);
                        } else {
                            // 信息提示
                            this.$message.error(msg);
                        }
                    }
                });
            } else if (this.activeName === 'second') {
                this.insert_form.resource_type_name = '操作'
                this.resource_type_code = 'OPERATION';
                this.$refs.insert_form.validate(async (valid) => {
                    if (valid) {
                        let { status, msg, data } = await this.create_operation({ ...this.insert_form });
                        if (status) {
                            // 关闭弹窗
                            this.insert_dialog_visible = false;
                            // 信息提示
                            this.$message.success(msg);
                        } else {
                            // 信息提示
                            this.$message.error(msg);
                        }
                    }
                });
            }
        },

        // 打开编辑框
        openEditDialog(menu) {
            // 记录当前menu
            this.current_menu = { ...menu };
            // 打开编辑框
            this.edit_dialog_visible = true;
            // 还原表单
            this.edit_form = { ...menu };
            if (this.edit_form.resource_type_code === 'MENU') {
                this.activeName = 'first'
            } else if (this.edit_form.resource_type_code === 'OPERATION') {
                this.activeName = 'second'
            }
        },
        // 编辑
        ...mapActions('menu', ['edit_menu']),
        ...mapActions('menu', ['edit_operation']),
        async handleEdit() {
            if (this.activeName === 'first') {
                let { menu_id } = this.edit_form;
                try {
                    let valid = await this.$refs.edit_form.validate();
                    if (valid) {
                        let payload = [menu_id, { ...this.edit_form }];
                        let { status, msg, data } = await this.edit_menu(payload);
                        if (status) {
                            // 关闭弹窗
                            this.edit_dialog_visible = false;
                            // 信息提示
                            this.$message.success(msg);
                        } else {
                            this.$message.error(msg);
                        }
                    }
                } catch (error) {
                    console.log(error);
                }
            } else if (this.activeName === 'second') {
                let { permission_id } = this.edit_form;
                try {
                    let valid = await this.$refs.edit_form.validate();
                    if (valid) {
                        let payload = [permission_id, { ...this.edit_form }];
                        let { status, msg, data } = await this.edit_operation(payload);
                        if (status) {
                            // 关闭弹窗
                            this.edit_dialog_visible = false;
                            // 信息提示
                            this.$message.success(msg);
                        } else {
                            this.$message.error(msg);
                        }
                    }
                } catch (error) {
                    console.log(error);
                }
            }

        },
        // 关闭弹窗，清除表单验证状态
        handleCloseDialog(formName) {
            this.$refs[formName].resetFields();
        },

        // 删除菜单
        ...mapActions('menu', ['remove_menu']),
        ...mapActions('menu', ['remove_operation']),
        handleRemove(menu) {
            if (menu.resource_type_code === 'MENU') {
                let { menu_id, permission_id } = menu;
                this.$confirm('此操作将永久删除该菜单, 是否继续?', '确认删除', { type: 'warning' })
                    .then(async () => {
                        let { status, msg } = await this.remove_menu([menu_id, { permission_id }]);
                        if (status) {
                            // 信息提示
                            this.$message.success(msg);
                        } else {
                            this.$message.error(msg);
                        }
                    }).catch(() => {
                        this.$message.info('已取消删除');
                    });
            } else if (menu.resource_type_code === 'OPERATION') {
                let { permission_id } = menu;
                console.log(permission_id);
                this.$confirm('此操作将永久删除该操作方式, 是否继续?', '确认删除', { type: 'warning' })
                    .then(async () => {
                        let { status, msg } = await this.remove_operation(permission_id);
                        if (status) {
                            // 信息提示
                            this.$message.success(msg);
                        } else {
                            this.$message.error(msg);
                        }
                    }).catch(() => {
                        this.$message.info('已取消删除');
                    });
            }
        },
        // 打开图标框
        ...mapActions('menu', ['load_icon']),
        async openIconDialog(menu) {
            // 记录当前menu
            this.current_menu = menu;
            this.icon_dialog_visible = true;
            let { status, msg } = await this.load_icon({ pageSize: 30, pageIndex: 1 });
            if (status) {
            } else {
                this.$message.error(msg);
            }
        },
        // 点击选择图标
        handleInsertIcon(icon) {
            this.current_icon = { ...icon };
        },
        // 添加图标
        ...mapActions('menu', ['create_icon']),
        async handleIcon() {
            if (this.current_icon) {
                let data = { ...this.current_menu, ...this.current_icon };
                let { status, msg } = await this.create_icon(data);
                if (status) {
                    // 关闭弹窗
                    this.icon_dialog_visible = false;
                    // 信息提示
                    this.$message.success(msg);
                } else {
                    // 信息提示
                    this.$message.error(msg);
                }
            } else {
                this.$message.error('请选择图标');
            }

        },
        // 选择页码
        async handleCurrentChange(page) {
            let { status, msg } = await this.load_icon({ pageSize: 30, pageIndex: page });
            if (status) {
            } else {
                this.$message.error(msg);
            }
        }
    },
}
</script>


<style lang="less" scoped>
.el-alert {
    margin-bottom: 15px;
}

.clearfix .el-button {
    float: right;
}

.el-alert {
    margin-bottom: 20px;
}

.outer {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    margin-left: 5px;
    border: 1px solid #eee;

    .inter {
        width: 9%;
        height: 109px;
        font-size: 10px;
        text-align: center;
        border-right: 1px solid #eee;
        border-bottom: 1px solid #eee;
    }

    .inter span {
        padding: 10px;
        color: #c5ceda;
    }

    .inter:hover {
        color: #5cb6ff;
    }

    .inter:hover span {
        color: #5cb6ff;
    }

    .click {
        background-color: #5cb6ff !important;
        color: white !important;
    }

    .click span {
        color: white !important;
    }
}
</style>